@import "common/mixins";
@import "common/variables";

.about {
  width: 100%;
  display: flex;
  justify-content: space-between;

  @media screen and (max-width: $m) {
    flex-direction: column;
  }

  .about__img {
    width: 50%;
    height: 800px;
    //background-color: #586049;
    background: url("../assets/img/about.jpg") bottom/cover no-repeat;

    @media screen and (max-width: $m) {
      width: 100%;
      max-height: 300px;
    }
  }

  .about__content {
    width: 50%;
    background-color: white;
    display: grid;
    place-items: center;

    @media screen and (max-width: $m) {
      width: 100%;
      padding: 30px 0;
    }

    .about__content__text {
      padding: 0 100px;
      line-height: 30px;
      font-style: italic;

      @media screen and (max-width: $m) {
        padding: 0 20px;
      }

      h2 {
        margin-bottom: 60px;
      }
    }
  }
}