@import "variables";

@mixin button() {
  padding: 15px 30px;
  margin: 0 40px;
  width: 120px;
  text-transform: uppercase;
  letter-spacing: 5px;
  color: gray;
  position: relative;
  z-index: 3;

  &:before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: #b5b2b2;
    opacity: 0.2;
    z-index: 2;
    pointer-events: none;
    animation: 0.5s linear both background-in;
  }

  &:after {
    content: '';
    position: absolute;
    top: -2px;
    left: -25%;
    width: 160%;
    height: 100%;
    border: 2px solid transparent;
    visibility: visible;
    z-index: 2;
    pointer-events: none;
    animation: 0.1s linear both borders-out;

    @media screen and (max-width: 900px) {
      left: 0;
      width: 100%;
      animation: 0.1s linear both borders-out-mobile;
    }
  }

  &:hover:before {
    animation: 0.5s linear both background-out;
  }

  &:hover:after {
    animation: 0.1s linear both borders-in;
  }
}

@keyframes background-out {
  100% {
    width: 0;
    height: 0;
    top: 50%;
    left: 50%;
  }
}

@keyframes background-in {
  0% {
    width: 0;
    height: 0;
    top: 50%;
    left: 50%;
  }
  100% {
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }
}

@keyframes borders-in {
  100% {
    top: -2px;
    left: -2px;
    width: 100%;
    height: 100%;
    border: 2px solid gray;
    transform: translateZ(-1px);
  }
}

@keyframes borders-out {
  0% {
    top: -2px;
    left: -2px;
    width: 100%;
    height: 100%;
    border: 2px solid gray;
    visibility: visible;
  }
  100% {
    top: -2px;
    left: -30%;
    width: 180%;
    height: 100%;
    border: 2px solid transparent;
    visibility: hidden;
  }
}

@keyframes borders-out-mobile {
  100% {
    border: 2px solid transparent;
  }
}
