@import "common/mixins";
@import "common/variables";

.main {
  width: 100%;
  height: 100vh;
  padding: 20px;
  box-sizing: border-box;

  .main__background {
    //background-color: $background;
    width: 100%;
    height: 100%;
    background: url("../assets/img/main.jpg") bottom/cover no-repeat;
    position: relative;
    overflow: hidden;

    @media (min-aspect-ratio: 5/2) {
      background: url("../assets/img/main.jpg") center/cover no-repeat;
    }

    @media screen and (max-width: $m) {
      background: url("../assets/img/main_mobile.jpg") center/cover no-repeat;
    }
  }

  .main__top {
    display: flex;
    justify-content: space-between;
    padding: 30px 60px 0 10px;
    box-sizing: border-box;

    @media screen and (max-width: $m) {
      padding: 20px 10px;
    }

    .main__top__logo {
      padding-left: 20px;
      letter-spacing: 5px;
      text-transform: uppercase;
      font-size: 36px;
    }

    .main__top__menu {

      ul {
        display: flex;
        justify-content: space-between;

        @media screen and (max-width: $m) {
          //display: none;
          position: absolute;
          height: calc(100vh - 105px);
          width: 40%;
          padding-top: 5px;
          top: 80px;
          right: -45%;
          flex-direction: column;
          justify-content: flex-start;
          background: white;
          z-index: 1;
          transition-duration: 1s;

          @media screen and (max-width: $xs) {
            width: 100%;
            right: -110%;
          }

          li.main__top__menu__button {
            margin-bottom: 5px;
          }
        }

        .main__top__menu__button {
          @include button();

          @media screen and (max-width: $m) {
            margin: 0;
          }
        }
      }

      .open-menu__btn {
        display: none;
        width: 40px;
        height: 40px;
        margin-right: 20px;
        border: none;
        background: none;
        padding: 0;

        @media screen and (max-width: $m) {
          display: flex;
          flex-direction: column;
          justify-content: space-around;
        }

        .top, .middle, .bottom {
          height: 3px;
          width: 100%;
          background-color: gray;
        }

        .top, .bottom {
          transition: .5s linear;
        }
      }

      &.open {
        ul {
          right: 0;
          transition-duration: 1s;
        }

        .open-menu__btn {
          justify-content: center;
          align-items: center;

          .middle {
            visibility: hidden;
          }

          .top {
            rotate: 45deg;
            transition: .5s linear;
          }

          .bottom {
            rotate: -45deg;
            transition: .5s linear;
            margin-top: -5px;
          }
        }
      }
    }
  }

  .main__center {
    position: absolute;
    top: 50%;
    width: 100%;
    transform: translateY(-50%);
    display: flex;
    justify-content: space-between;

    @media screen and (max-width: $m) {
      flex-direction: column;
      top: 23%;
    }

    .main__center__text {
      text-align: center;
      text-transform: uppercase;
      width: 50%;

      @media screen and (max-width: $m) {
        width: auto;
      }
    }

    h1 {
      font-size: 84px;
      letter-spacing: 10px;
      color: #5d5d5d;
      margin: 0;

      @media screen and (max-width: $m) {
        font-size: 54px;
      }
    }
  }
}
