@import "common/mixins";
@import "common/variables";

.subscribe {
  background-color: $background;
  padding: 100px 0;
  display: flex;
  flex-direction: column;
  align-items: center;

  h2 {
    margin-bottom: 40px;
  }

  p {
    letter-spacing: 1px;
  }

  .subscribe__input {
    border: none;
    background-color: white;
    padding: 15px 20px;
    width: 300px;

    &:focus {
      outline-color: gray;
    }

  }

  .subscribe__button {
    @include button();
    margin-top: 20px;
  }
}
