@import "common/mixins";
@import "common/variables";

.footer {
  margin: 70px;
  display: flex;
  justify-content: space-between;


  @media screen and (max-width: $l) {
    flex-direction: column;
    align-items: center;
    margin: 30px;
  }

  .footer__socials {
    display: flex;
    justify-content: space-between;

    @media screen and (max-width: $l) {
      margin-bottom: 30px;
    }

    .footer__socials__item {
      width: 50px;
      height: 50px;
      margin: 0 10px;
      background-color: #586049;
      border-radius: 50%;
      color: $background;
      display: flex;
      justify-content: center;
      align-items: center;
      font-size: 24px;
    }
  }

  .footer__links {
    display: flex;
    justify-content: space-between;
    align-items: center;

    @media screen and (max-width: $m) {
      margin-top: 30px;
      flex-direction: column;
      align-items: normal;
    }

    .footer__links__item {
      @include button();

      @media screen and (max-width: $m) {
        margin-bottom: 5px;
      }
    }
  }
}
