@import "common/mixins";
@import "common/variables";

.articles {
  background-color: $background;
  padding: 150px 50px;
  @media screen and (max-width: $m) {
    padding: 20px 0;
  }

  .articles__row {
    margin: 150px 100px 0;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    align-items: center;

    @media screen and (max-width: $l) {
      flex-direction: column;
    }

    @media screen and (max-width: $m) {
      margin: 50px 0;
    }

    article {
      width: 350px;
      background-color: white;
      box-shadow: 5px 10px 25px gray;
      margin: 15px 15px;

      @media screen and (max-width: $m) {
        width: calc(100% - 20px);
        margin: 15px 10px;
      }

      .article__img {
        width: 100%;
        min-height: 250px;
        //background-color: #586049;
      }

      .article__content {
        margin: 70px 50px;
        line-height: 30px;
        font-style: italic;
        font-size: 18px;
        letter-spacing: 1px;
        position: relative;

        &::before {
          content: '';
          width: 30px;
          height: 1px;
          border-top: 1px solid gray;
          position: absolute;
          top: -20px;
          left: 50%;
          transform: translateX(-50%);
        }
      }

      &:nth-child(1) .article__img { background: url("../assets/img/article1.jpg") bottom/cover no-repeat; }
      &:nth-child(2) .article__img { background: url("../assets/img/article2.jpg") bottom/cover no-repeat; }
      &:nth-child(3) .article__img { background: url("../assets/img/article3.jpg") bottom/cover no-repeat; }
    }
  }
}