@import "common/social_font";
@import "main";
@import "articles";
@import "about";
@import "subscribe";
@import "footer";
@import "common/mixins";
@import "common/variables";

* {
  font-family: sans-serif;
}

body {
  margin: 0;
  padding: 0;
  color: gray;

  section {
    text-align: center;
  }

  ul {
    margin: 0;
    padding: 0;
    list-style-type: none;
  }

  h2, h3 {
    letter-spacing: 5px;
    position: relative;
    text-transform: uppercase;
  }

  h2::before {
    content: '';
    width: 50px;
    height: 1px;
    border-top: 1px solid gray;
    position: absolute;
    top: 50px;
    left: 50%;
    transform: translateX(-50%);
  }
}
